<script>
import { mapGetters, mapActions } from 'vuex'
import $ from 'jquery'
import { common } from '@/utils'
import _game from "@/utils/api/game";

export default {
  name: 'SoccerHF',
  data () {
    return {
      gname: '足球',
      HFListAll: {
        show: true,
        showNoData: '目前無資料',
        headers: [
          { text: '主/主', handicap:'HH', show: true, align: 'center', sortable: false },
          { text: '主/和', handicap:'HD', show: true, align: 'center', sortable: false },
          { text: '主/客', handicap:'HA', show: true, align: 'center', sortable: false },
          { text: '和/主', handicap:'DH', show: true, align: 'center', sortable: false },
          { text: '和/和', handicap:'DD', show: true, align: 'center', sortable: false },
          { text: '和/客', handicap:'DA', show: true, align: 'center', sortable: false },
          { text: '客/主', handicap:'AH', show: true, align: 'center', sortable: false },
          { text: '客/合', handicap:'AD', show: true, align: 'center', sortable: false },
          { text: '客/客', handicap:'AA', show: true, align: 'center', sortable: false }
        ],
        list: []
      },
      // 拆帳選擇
      selectChangeItems: {
        selected: 1,
        options: [
          { text: '100％', value: 1 },
          { text: '自己', value: 0 }
        ]
      },
      
      // 秒數倒數更新
      reciprocalItems: {
        selected: '',
        options: [
          { text: '每180秒更新', value: 180 },
          { text: '每60秒更新', value: 60 },
          { text: '每5秒更新', value: 5 },
          { text: '不更新', value: '' }
        ],
        // 倒數 timer
        updateListTimer: null,
        // 當前秒數
        currentSpeed: '',
        // 確定更新秒數
        confirmSpeed: ''
      },
      dataList: [],
      game_time: '',
      occupy: '',
      count_date: '',
      main_team: '',
      visit_team: '',
      league_name: '',
      transType: '',
      posting: '',
      game_serial_number: '',
      game_type: ''
    }
  },
  computed: {
  },
  watch: {
    // 每秒更新切換
    'reciprocalItems.selected' (value) {
      this.updateTableList()
    },
    'reciprocalItems.currentSpeed' (value) {
      // 倒數為0秒時刷新列表
      if (value === 0) {
        this.getPlayTypeDetail(this.selectChangeItems.selected)
      }
    },
    // (拆帳)切換
    'selectChangeItems.selected' (value) {
      // this.occupy = value
      // console.log('occupy',value)
      this.getPlayTypeDetail(value)
    },
    'selectTimeItems.selected' (value) {
      clearInterval(this.interval)
      this.selectedTimer = value
      if (value === 0) {
        this.intervalTimer = 0
      } else if (value === 0 || value === 5 || value === 60 || value === 180) {
        this.intervalTimer = 0
        this.setIntervalTime()
      }
    }
  },
  mounted () {
    this.selectChangeItems.selected = parseInt(this.$route.query.occupy)
    this.getPlayTypeDetail()
  },
  methods: {
    /* 載入資料
     * @param occupy
     */
    async getPlayTypeDetail (occupy) {
      console.log('>>>', occupy, occupy !== undefined)
      // 載入狀態(開始)
      this.loadingStatus = true
      let params = {
        occupy:  occupy !== undefined ? occupy : this.$route.query.occupy,
        posting: this.$route.query.Posting,
        game_serial_number: this.$route.query.gameSN,
        game_type: this.$route.query.game_type,
        play_type: 'hafu'
      }
      console.log(occupy)
      console.log('params >>>', params)
      let res = await _game.getPlayTypeDetail(params)
      this.main_team = res.main_team
      this.visit_team = res.visit_team
      this.count_date = res.count_date
      this.game_time = res.game_time
      this.league_name = res.league_name
      this.transType = res.transType
      
      // 對應盤口排序
      let dataHF = []
      for(let item in res.dataList){
        console.log(res.dataList[item])
        if(res.dataList[item].handicap==='HH'){
          dataHF[0] = res.dataList[item]
        }else if(res.dataList[item].handicap==='HD'){
          dataHF[1] = res.dataList[item]
        }else if(res.dataList[item].handicap==='HA'){
          dataHF[2] = res.dataList[item]
        }else if(res.dataList[item].handicap==='DH'){
          dataHF[3] = res.dataList[item]
        }else if(res.dataList[item].handicap==='DD'){
          dataHF[4] = res.dataList[item]
        }else if(res.dataList[item].handicap==='DA'){
          dataHF[5] = res.dataList[item]
        }else if(res.dataList[item].handicap==='AH'){
          dataHF[6] = res.dataList[item]
        }else if(res.dataList[item].handicap==='AD'){
          dataHF[7] = res.dataList[item]
        }else if(res.dataList[item].handicap==='AA'){
          dataHF[8] = res.dataList[item]
        }
      }
      console.log(dataHF)
      // 處理完再塞回
      this.HFListAll.list = dataHF
      // console.log('~~~', dataHF)
       // 載入狀態(結束))
      this.loadingStatus = false
    },
    /**
     * 秒數倒數
     * @params  [speed]  倒數秒數
     **/
    startCountdown (speed) {
      this.reciprocalItems.currentSpeed = speed
      this.reciprocalItems.updateListTimer = setInterval(() => {
        this.reciprocalItems.currentSpeed--
        // 秒數為負數，則重新倒數
        if (this.reciprocalItems.currentSpeed < 0) {
          // 清除 timer
          clearInterval(this.reciprocalItems.updateListTimer)
          // 重新倒數秒數
          this.startCountdown(this.reciprocalItems.confirmSpeed)
        }
      }, 1000)
    },
    // 啟動秒數倒數刷新表格資料
    updateTableList () {
      this.getPlayTypeDetail(this.selectChangeItems.selected)

      // 確定更新秒數
      this.reciprocalItems.confirmSpeed = this.reciprocalItems.selected

      // 清除 timer
      clearInterval(this.reciprocalItems.updateListTimer)

      // 秒數為負數，則不更新倒數功能
      if (this.reciprocalItems.selected < 0) {
        // 當前秒數設置空值
        this.reciprocalItems.currentSpeed = ''
        return
      }
      // 倒數秒數
      this.startCountdown(this.reciprocalItems.confirmSpeed)
    }
  }
}
</script>